import { SelectedFile } from '../contexts/AuthContext';
import initSqlJs from 'sql.js';

// Initialize SQLite
let SQL: any = null;
let db: any = null;

interface Notification {
  id: string;
  app: string;
  title: string;
  text: string;
  datetime: Date;
}

export interface DbFile extends SelectedFile {
  loaded: boolean;
}

// Function to initialize SQL.js
export const initializeSqlJs = async (): Promise<void> => {
  if (!SQL) {
    // Load SQL.js from CDN - this is the browser-friendly way
    const sqlPromise = initSqlJs({
      locateFile: (file) =>
        `https://cdnjs.cloudflare.com/ajax/libs/sql.js/1.8.0/${file}`,
    });
    SQL = await sqlPromise;
  }
};

// Function to load a SQLite database from Google Drive
export const loadDatabaseFromDrive = async (
  accessToken: string,
  fileId: string
): Promise<Uint8Array> => {
  try {
    // Fetch the file from Google Drive
    const response = await fetch(
      `https://www.googleapis.com/drive/v3/files/${fileId}?alt=media`,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      }
    );

    if (!response.ok) {
      throw new Error(`Failed to fetch database file: ${response.statusText}`);
    }

    // Convert the response to an array buffer
    const arrayBuffer = await response.arrayBuffer();

    // Convert to Uint8Array which SQL.js expects
    return new Uint8Array(arrayBuffer);
  } catch (error) {
    console.error('Error loading database from Drive:', error);
    throw error;
  }
};

// Function to open a database
export const openDatabase = async (data: Uint8Array): Promise<void> => {
  if (!SQL) {
    await initializeSqlJs();
  }

  if (SQL) {
    if (db) {
      db.close();
    }
    db = new SQL.Database(data);
  } else {
    throw new Error('SQL.js not initialized');
  }
};

// Function to get notifications from the database
export const getNotifications = async (
  limit: number = 100,
  offset: number = 0
): Promise<Notification[]> => {
  if (!db) {
    throw new Error('Database not opened');
  }

  try {
    // Query the database for notifications
    // Adjust this query based on your actual database schema
    const query = `
      SELECT id, app, title, text, datetime
      FROM notifications
      ORDER BY datetime DESC
      LIMIT ? OFFSET ?
    `;

    const result = db.exec(query, [limit, offset]);

    if (result.length === 0 || result[0].values.length === 0) {
      return [];
    }

    // Convert the result to Notification objects
    return result[0].values.map((row: any[]) => {
      const [id, app, title, text, datetime] = row;
      return {
        id: String(id),
        app: String(app),
        title: String(title),
        text: String(text),
        datetime: new Date(String(datetime)),
      };
    });
  } catch (error) {
    console.error('Error querying database:', error);
    throw error;
  }
};

// Function to search notifications
export const searchNotifications = async (
  searchTerm: string
): Promise<Notification[]> => {
  if (!db) {
    throw new Error('Database not opened');
  }

  try {
    // Search the database for notifications matching the search term
    const query = `
      SELECT id, app, title, text, datetime
      FROM notifications
      WHERE app LIKE ? OR title LIKE ? OR text LIKE ?
      ORDER BY datetime DESC
    `;

    const searchPattern = `%${searchTerm}%`;
    const result = db.exec(query, [
      searchPattern,
      searchPattern,
      searchPattern,
    ]);

    if (result.length === 0 || result[0].values.length === 0) {
      return [];
    }

    // Convert the result to Notification objects
    return result[0].values.map((row: any[]) => {
      const [id, app, title, text, datetime] = row;
      return {
        id: String(id),
        app: String(app),
        title: String(title),
        text: String(text),
        datetime: new Date(String(datetime)),
      };
    });
  } catch (error) {
    console.error('Error searching database:', error);
    throw error;
  }
};

// Function to delete a notification
export const deleteNotification = async (id: string): Promise<boolean> => {
  if (!db) {
    throw new Error('Database not opened');
  }

  try {
    // Delete the notification with the given ID
    const query = 'DELETE FROM notifications WHERE id = ?';
    db.run(query, [id]);
    return true;
  } catch (error) {
    console.error('Error deleting notification:', error);
    return false;
  }
};

// Function to archive a notification
export const archiveNotification = async (id: string): Promise<boolean> => {
  if (!db) {
    throw new Error('Database not opened');
  }

  try {
    // Update the notification to mark it as archived
    // Assuming there's an 'archived' column in your schema
    const query = 'UPDATE notifications SET archived = 1 WHERE id = ?';
    db.run(query, [id]);
    return true;
  } catch (error) {
    console.error('Error archiving notification:', error);
    return false;
  }
};

// Function to close the database connection
export const closeDatabase = (): void => {
  if (db) {
    db.close();
    db = null;
  }
};
