import React, { useState, useEffect, useCallback } from 'react';
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Card,
  Spinner,
  Dropdown,
} from 'react-bootstrap';
import { useAuth } from '../contexts/AuthContext';
import * as SqliteService from '../services/SqliteService';

interface Notification {
  id: string;
  app: string;
  title: string;
  text: string;
  datetime: Date;
}

const NotificationsPage: React.FC = () => {
  const { user, selectedFiles } = useAuth();
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [currentDbFile, setCurrentDbFile] =
    useState<SqliteService.DbFile | null>(null);
  const [dbFiles, setDbFiles] = useState<SqliteService.DbFile[]>([]);

  // Initialize SQL.js and prepare database files
  useEffect(() => {
    const initSql = async () => {
      try {
        setIsLoading(true);
        await SqliteService.initializeSqlJs();

        // Convert selectedFiles to DbFiles
        const files = selectedFiles.map((file) => ({
          ...file,
          loaded: false,
        }));

        setDbFiles(files);

        // If there are files, set the first one as current
        if (files.length > 0) {
          setCurrentDbFile(files[0]);
        }

        setIsLoading(false);
      } catch (error) {
        console.error('Failed to initialize SQL.js:', error);
        setError(
          'Failed to initialize database. Please refresh and try again.'
        );
        setIsLoading(false);
      }
    };

    initSql();

    // Cleanup
    return () => {
      SqliteService.closeDatabase();
    };
  }, [selectedFiles]);

  // Load the current database file
  useEffect(() => {
    const loadDatabase = async () => {
      if (!currentDbFile || !user?.accessToken) return;

      try {
        setIsLoading(true);
        setError(null);

        // Load database from Google Drive
        const dbData = await SqliteService.loadDatabaseFromDrive(
          user.accessToken,
          currentDbFile.id
        );

        // Open the database
        await SqliteService.openDatabase(dbData);

        // Mark the current file as loaded
        setDbFiles((prevFiles) =>
          prevFiles.map((file) =>
            file.id === currentDbFile.id ? { ...file, loaded: true } : file
          )
        );

        // Load notifications
        const notifications = await SqliteService.getNotifications();
        setNotifications(notifications);

        setIsLoading(false);
      } catch (error) {
        console.error('Failed to load database:', error);
        setError(
          'Failed to load the database file. Please try another file or refresh the page.'
        );
        setIsLoading(false);
      }
    };

    loadDatabase();
  }, [currentDbFile, user?.accessToken]);

  // Handle search
  const handleSearch = useCallback(async () => {
    if (!searchTerm.trim()) {
      // If search is empty, get all notifications
      const notifications = await SqliteService.getNotifications();
      setNotifications(notifications);
      return;
    }

    try {
      const results = await SqliteService.searchNotifications(searchTerm);
      setNotifications(results);
    } catch (error) {
      console.error('Search failed:', error);
      setError('Failed to search the database. Please try again.');
    }
  }, [searchTerm]);

  // Handle file selection change
  const handleFileChange = (fileId: string) => {
    const file = dbFiles.find((f) => f.id === fileId);
    if (file) {
      setCurrentDbFile(file);
    }
  };

  // Handle delete notification
  const handleDelete = async (id: string) => {
    try {
      const success = await SqliteService.deleteNotification(id);
      if (success) {
        // Remove from the current list
        setNotifications((prev) =>
          prev.filter((notification) => notification.id !== id)
        );
      } else {
        setError('Failed to delete the notification.');
      }
    } catch (error) {
      console.error('Delete failed:', error);
      setError('Failed to delete the notification. Please try again.');
    }
  };

  // Handle archive notification
  const handleArchive = async (id: string) => {
    try {
      const success = await SqliteService.archiveNotification(id);
      if (success) {
        // Refresh the list
        handleSearch();
      } else {
        setError('Failed to archive the notification.');
      }
    } catch (error) {
      console.error('Archive failed:', error);
      setError('Failed to archive the notification. Please try again.');
    }
  };

  // Format date for display
  const formatDate = (date: Date): string => {
    return date.toLocaleString('en-US', {
      dateStyle: 'short',
      timeStyle: 'short',
    });
  };

  return (
    <Container className="py-4">
      <h2 className="text-center mb-4">Saved Notifications</h2>

      {error && (
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      )}

      {/* Database File Selector */}
      {dbFiles.length > 1 && (
        <Row className="mb-4">
          <Col>
            <Dropdown>
              <Dropdown.Toggle variant="primary" id="dropdown-basic">
                {currentDbFile?.name || 'Select Database File'}
              </Dropdown.Toggle>

              <Dropdown.Menu>
                {dbFiles.map((file) => (
                  <Dropdown.Item
                    key={file.id}
                    onClick={() => handleFileChange(file.id)}
                    active={currentDbFile?.id === file.id}
                  >
                    {file.name}
                  </Dropdown.Item>
                ))}
              </Dropdown.Menu>
            </Dropdown>
          </Col>
        </Row>
      )}

      {/* Search Box */}
      <Row className="mb-4">
        <Col>
          <div className="input-group">
            <Form.Control
              type="text"
              placeholder="Filter your notifications..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyUp={(e) => {
                if (e.key === 'Enter') {
                  handleSearch();
                }
              }}
            />
            <Button variant="outline-secondary" onClick={handleSearch}>
              <svg
                width="17"
                height="18"
                viewBox="0 0 17 18"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                className="search-icon"
              >
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M1 8.13387C0.99954 5.06715 3.1655 2.42715 6.17325 1.82843C9.18104 1.22972 12.1929 2.83905 13.3668 5.6722C14.5408 8.50539 13.5499 11.7733 11.0002 13.4774C8.45044 15.1814 5.05204 14.8472 2.88327 12.6788C1.67759 11.4736 1.00015 9.83874 1 8.13387Z"
                  stroke="#AAAAAA"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
                <path
                  d="M12.4286 13.1327L16 16.7041"
                  stroke="#AAAAAA"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </Button>
          </div>
        </Col>
      </Row>

      {/* Loading Spinner */}
      {isLoading && (
        <div className="text-center my-5">
          <Spinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
          </Spinner>
          <p className="mt-2">Loading notifications...</p>
        </div>
      )}

      {/* Notifications List */}
      {!isLoading && notifications.length === 0 && (
        <div className="text-center my-5">
          <p>No notifications found.</p>
        </div>
      )}

      {!isLoading &&
        notifications.map((notification) => (
          <Card className="mb-3 shadow-sm" key={notification.id}>
            <Card.Body>
              <div className="d-grid gap-3 fs-5">
                <div className="row">
                  <div className="col">
                    <span className="text-muted fs-6">
                      {formatDate(notification.datetime)}
                    </span>{' '}
                    -<strong>{notification.app}</strong> -
                    <span>{notification.title}</span>
                  </div>
                </div>
                <div className="row fs-4 text-body-primary">
                  <div className="col">
                    <span>{notification.text}</span>
                  </div>
                </div>
                <div className="row row-cols-auto">
                  <div className="col">
                    <Button
                      variant="primary"
                      className="me-2"
                      onClick={() => {
                        // Handle open action
                        window.open(
                          `https://www.google.com/search?q=${encodeURIComponent(
                            notification.title + ' ' + notification.text
                          )}`,
                          '_blank'
                        );
                      }}
                    >
                      Open
                    </Button>
                    <Button
                      variant="info"
                      className="me-2"
                      onClick={() => {
                        // Handle search action
                        window.open(
                          `https://www.google.com/search?q=${encodeURIComponent(
                            notification.title + ' ' + notification.text
                          )}`,
                          '_blank'
                        );
                      }}
                    >
                      Search
                    </Button>
                    <Button
                      variant="warning"
                      className="me-2"
                      onClick={() => handleArchive(notification.id)}
                    >
                      Archive
                    </Button>
                    <Button
                      variant="danger"
                      onClick={() => handleDelete(notification.id)}
                    >
                      Delete
                    </Button>
                  </div>
                </div>
              </div>
            </Card.Body>
          </Card>
        ))}
    </Container>
  );
};

export default NotificationsPage;
